body {
    margin: 0 auto;
    font-family: "PT Sans", "Helvetica", sans-serif;
    text-transform: uppercase;
    text-align: center;
}

.nav__list {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}

.nav__item {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    background-color: #81b3d2;
}

.nav__item_logo {
    height: 58px;
    background-color: #ffffff;
}

.nav__link {
    text-decoration: none;
    color: #ffffff;
    line-height: 56px;
}

.nav__logo {
    max-width: 100%;
    height: auto;
}

.nav__link_logo {
    width: 102px;
    height: 83px;
    margin-top: 25px;
    background-color: transparent;
}

.close-button {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 60px;
    height: 60px;
    border: none;
    background: #81b3d2;
}

.intro {
    min-height: 158px;
    margin: 0;
    padding: 52px 0 0;
    background-color: #e4f1f9;
    background-image: url(../src/bg-img/bg-intro-triangle-desktop.svg), url(../src/bg-img/bg-back-mobile@2x.jpg);
    background-size: auto, cover;
    background-repeat: no-repeat;
    background-position: bottom;
}

.intro_first-page {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    min-height: 389px;
}

.intro__banner {
    width: 280px;
}

.content__greeting {
    padding: 40px 6%;
    padding-bottom: 15px;
}

.content__header {
    font-size: 18px;
    line-height: 26px;
}

.content__text {
    margin: 0;
    font-size: 14px;
    line-height: 26px;
    color: #333333;
}

.advantage, .advantages {
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.advantage {
    color: #ffffff;
    background-color: #81b3d2;
}

.advantage:first-child {
    padding-top: 36px;
    background-image: url(bg-img/bg-triangle.svg);
    background-repeat: no-repeat;
    background-position: 50% top;
    background-size: auto;
}

.advantage__inner {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.advantage__text {
    flex-grow: 1;
    width: 320px;
    padding: 40px 0;
}

.advantage__header {
    margin: 0;
    font-size: 18px;
    line-height: 26px;
}

.advantage__number {
    margin: 12px 0;
    font-size: 14px;
    line-height: 21px;
}

.advantage__paragraph {
    margin: 0;
    font-size: 14px;
    line-height: 21px;
}

.advantage__photo {
    width: 320px;
    flex-shrink: 0;
    height: 260px;
    line-height: 0;
    background-color: #aab7a5;
}

.advantages img {
    max-width: 100%;
    height: auto;
    border: 0;
}

.advantages__card_short {
    color: #333333;
    background-color: #ededed;
}

.features {
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    list-style: none;
    color: #333333;
    background-color: #ffffff;
}

.features__header {
    margin: 0;
    font-size: 18px;
    line-height: 26px;
}

.features__item {
    padding: 20px 5%;
    display: flex;
    text-align: left;
}

.features__icon-wrapper {
    display: block;
    min-width: 37px;
    height: 37px;
}

.features__text {
    padding-left: 20px;
}

.features__header {
    margin: 0;
    font-size: 18px;
    line-height: 26px;
}

.features__paragraph {
    margin: 0;
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
}

.location__header {
    margin: 0;
    padding: 0;
    font-size: 21px;
    line-height: 26px;
}

.location__text {
    margin: 0;
    font-size: 14px;
    line-height: 21px;
}

.button {
    display: block;
    width: 100%;
    height: 58px;
    margin: 30px 0;
    padding: 0;
    font-weight: 700;
    font-size: 21px;
    line-height: 58px;
    color: #ffffff;
    text-transform: uppercase;
    background-color: #81b3d2;
    border: none;
    outline: none;
}

.location__link {
    text-decoration: none;
}

.map, .map__source {
    width: 100%;
    margin: 0;
    padding: 0;
}

.map {
    height: 350px;
    line-height: 0;
    background-color: #cbe0a3;
    background-image: url(bg-img/bg-map-desktop@1x.jpg);
    background-repeat: no-repeat;
    background-position: 50% top;
    background-size: auto 350px;
}

.footer {
    min-height: 230px;
    margin: 0 20px;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.footer__tag {
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    color: #000000;
}

.social-list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.social-list__item {
    margin-right: 6px;
    margin-bottom: 6px;
}

.social-list__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    color: #000000;
    background-color: #81b3d2;
    text-decoration: none;
}

.social-list__link img {
    filter: invert(100%);
}

.footer__copyright {
    display: flex;
    justify-content: flex-end;
}

.footer__copyright-text {
    padding-right: 12px;
    line-height: 48px;
}

.footer__copyright-link {
    padding-top: 5px;
}

.photo-list {
    width: 100%;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    list-style: none;
    text-align: left;
}

.photo-list__item {
    padding: 23px 0;
    font-size: 14px;
    position: relative;
}

.photo-list__content {
    margin: 0;
    padding: 0;
    position: relative;
}

.photo-list__photo {
    width: 280px;
    height: 280px;
    background-color: #aab7a5;
    display: block;
}

.photo-list img {
    max-width: 100%;
    height: auto;
}

.photo-list__text {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.photo-list__header {
    font-size: 14px;
    line-height: 24px;
    font-weight: bold;
}

.photo-list__likes {
    margin-top: 5px;
    display: flex;
    align-items: flex-end;
}

.photo-list__likes-icon-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.photo-list__likes-count {
    padding-top: 5px;
    padding-left: 10px;
    font-weight: bold;
    font-size: 14px;
    line-height: 14px;
    display: block;
    color: #000000;
}

.photo-list__author {
    text-transform: none;
}

.presentation__title {
    margin: 0;
    padding: 40px 6%;
}

.presentation__header {
    margin: 0;
    font-size: 18px;
    line-height: 26px;
}

.presentation__text {
    margin: 0;
    font-size: 14px;
    line-height: 24px;
}

.video {
    width: 320px;
    margin: 0 auto;
    position: relative;
}

.video__screen {
    min-height: 201px;
    margin: 0;
    padding: 0;
    background-color: #cbe0a3;
    background-image: url("../src/bg-img/video-mobile@2x.jpg");
    background-repeat: no-repeat;
    background-position: 50% top;
    background-size: cover;
}

.video__controls {
    min-height: 45px;
    min-width: 320px;
    margin: 0;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    background-color: rgba(0, 0, 0, 0.5);
}

.video__controls-button {
    min-width: 70px;
    min-height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
}

.video__controls-button img {
    filter: invert(100%) brightness(100);
}

.video__controls-length {
    width: 320px;
    min-height: 45px;
    position: absolute;
    top: -45px;
    background-color: rgba(0, 0, 0, 0.5);
}

.video__controls-length::after, .video__controls-length::before {
    content: "";
    position: absolute;
    top: 50%;
    display: block;
    transform: translate(0, -50%);
}

.video__controls-length::before {
    right: 22px;
    background-color: rgba(255, 255, 255, 0.3);
    left: 22px;
    height: 4px;
}

.video__controls-length::after {
    left: 50px;
    width: 9px;
    height: 10px;
    background-color: #ffffff;
    border-radius: 50%;
}

@media (min-width: 768px) {

    .App {
        max-width: 1200px;
        margin: 0 auto;
    }

    .intro_first-page {
        min-height: 518px;
        padding-top: 78px;
    }

    .close-button {
        display: none;
    }

    .nav__list {
        flex-direction: row;
    }

    .nav__item {
        display: block;
        width: 20%;
        background-color: #ffffff;
    }

    .nav__item:nth-child(3) {
        order: 4;
    }

    .nav__item:nth-child(4) {
        order: 5;
    }

    .nav__item:nth-child(5) {
        order: 3;
    }

    .nav__link {
        color: #000000;
    }

    .nav__logo {
        height: 150%;
    }

    .intro__banner {
        width: 460px;
    }

    .content__title,
    .content__greeting {
        padding: 50px 12%;
    }

    .content__header {
        font-size: 21px;
        margin-bottom: 35px;
    }

    .content__header_bigger {
        font-size: 30px;
    }

    .content__header_smaller {
        line-height: 24px;
    }

    .advantage, .advantages {
        flex-direction: row;
    }

    .advantage:first-child {
        padding-top: 0;
        background-image: none;
    }

    .advantage__inner {
        flex-direction: row;
        padding-bottom: 0;
    }

    .advantage:nth-child(even) .advantage__text {
        order: 1;
    }

    .advantage__text {
        width: auto;
        padding: 40px 20px;
    }

    .advantage__header {
        font-size: 21px;
    }

    .advantage__number {
        margin: 20px 0;
    }

    .advantage__paragraph {
        margin: 0;
        font-size: 14px;
        line-height: 21px;
    }

    .advantage__photo {
        width: 512px;
        height: 256px;
    }

    .advantages__card_short {
        flex-grow: 1;
        width: 33.33%;
    }

    .features {
        flex-direction: row;
    }

    .features__item {
        flex-grow: 1;
        width: 27.39%;
        min-height: 256px;
        padding: 30px 20px;
        flex-direction: column;
        text-align: center;
    }

    .features__icon {
        width: 100%;
        height: 76px;
        background-size: 75px 75px;
    }

    .features__header {
        font-size: 21px;
        margin-bottom: 20px;
    }

    .features__text {
        padding-top: 20px;
        padding-left: 0;
    }

    .location__find {
        padding: 50px 12%;
    }

    .location__header {
        margin-bottom: 35px;
        font-size: 30px;
    }

    .footer {
        min-height: auto;
        margin: 0 60px;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    .photo-list {
        width: 648px;
        margin: 0 auto;
        flex-direction: row;
    }

    .photo-list__item {
        position: static;
        padding: 0;
    }

    .photo-list__item:first-child .photo-list__photo {
        width: 648px;
        height: 400px;
    }

    .photo-list__text {
        min-height: 80px;
        margin-top: 0;
        flex-direction: row;
        color: #333333;
        background-color: #f2f2f2;
    }

    .photo-list__description {
        margin-top: 15px;
        margin-left: 20px;
    }

    .photo-list__likes {
        margin-top: 0;
        width: 85px;
        align-items: center;
    }

    .photo-list__photo {
        width: 324px;
        height: 267px;
    }

    .presentation__title {
        padding: 50px 12%;
    }

    .presentation__header {
        margin-bottom: 35px;
        font-size: 30px;
        line-height: 26px;
    }

    .video {
        width: 648px;
    }

    .video__screen {
        min-height: 499px;
    }

    .video__controls {
        min-height: 93px;
        margin: -93px 0 0;
        position: static;
    }

    .video__controls-length {
        position: relative;
        top: 0;
        flex-grow: 1;
        width: auto;
        background: 0 0;
    }
}

@media (min-width: 1200px) {
    .App {
        box-shadow: 0 0 40px 0 #dbdbdb;
    }

    .nav__logo {
        width: 188px;
        height: 96px;
    }

    .intro_first-page {
        padding-top: 170px;
    }

    .photo-list {
        width: auto;
    }

    .photo-list__item:first-child .photo-list__photo {
        width: 1200px;
    }

    .photo-list__text {
        width: 100%;
        position: absolute;
        bottom: 0;
        color: #ffffff;
        background-color: rgba(0, 0, 0, 0.5);
    }

    .photo-list__header {
        color: #ffffff;
    }

    .photo-list__likes img {
        filter: invert(100%);
    }

    .photo-list__likes-count {
        color: #ffffff;
    }

    .photo-list__photo {
        width: 400px;
    }

    .video {
        width: 800px;
    }

    .video__screen {
        min-height: 500px;
    }
}

@media (min-width: 768px) {
    .intro {
        background-image: url(bg-img/bg-intro-triangle-desktop.svg), url(bg-img/bg-back-desktop@2x.jpg);
        background-size: auto, cover;
    }
}

